import React from 'react';
import styled from 'styled-components';


const Call2Act = ({ text, href, accent, target}) => (
  <Call2Button accent={accent}>
    <a href={href} target={target}>
    {text}  <big></big>
  </a>
  </Call2Button>
);


const Call2Button = styled.div`
  display: inline-block;
  font-weight: 400;
  text-align: center;
  vertical-align: middle;
  border-radius: 100px;
  border: 2px solid;
  border-color: ${props => props.theme.color.grayscale.S1000};
  line-height: 24px;
  padding: 11px 25px;
  margin: 35px 0;
  -webkit-transition: border-color .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
  transition: border-color .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
  -o-transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
  ${props =>
    props.accent &&
    `border-color: ${
      props.accent === 'inverse'
        ? props.theme.color.white.regular
        : props.theme.color.grayscale.S1000
  }}`};

  a {
    text-decoration: none;
    color: ${props => props.theme.color.grayscale.S1000};
    -webkit-transition: color .15s ease-in-out;
    transition: color .15s ease-in-out;
    -o-transition: color .15s ease-in-out;
    ${props =>
      props.accent &&
      `color: ${
        props.accent === 'inverse'
          ? props.theme.color.white.regular
          : props.theme.color.grayscale.S1000
    }}`};

  } 
  &:hover {
    a{
      color: ${props => props.theme.color.primary.S500};
    }
    border-color: ${props => props.theme.color.primary.S500};
  }

  
`;

export default Call2Act;
